              <span v-if="total.to !== total.from">
                - {{ total.to }} {{ currencySymbol }}
              </span>
            </del>
          </div>

          <div class="total-with-vat" v-if="vatEnable">
            <span>Cena s DPH ({{ vatValue }}%)</span>
            <span class="price">
              {{ totalWith21VAT.from }} {{ currencySymbol }}
              <span v-if="totalWith21VAT.to !== totalWith21VAT.from">
                - {{ totalWith21VAT.to }} {{ currencySymbol }}
              </span>
            </span>
          </div>
          <div class="total-without-vat">
            <span>
