            <span class="price">
              <span v-if="total.sum || total.sum === 0">
                <span
                  v-if="totalDiscount && parseFloat(totalDiscountValue) > 0"
                >
                  {{ totalWithDiscount.sum }} {{ currencySymbol }}
                </span>
                <span v-else>{{ total.sum }} {{ currencySymbol }}</span>
              </span>
              <span v-else>
                <span
                  v-if="totalDiscount && parseFloat(totalDiscountValue) > 0"
                >
                  {{ totalWithDiscount.from }} {{ currencySymbol }} -
                  {{ totalWithDiscount.to }} {{ currencySymbol }}
                </span>
                <span v-else>
                  {{ total.from }} {{ currencySymbol }} - {{ total.to }}
                  {{ currencySymbol }}
                </span>
              </span>
            </span>
          </div>
        </div>
      </div>
    </div>

    <div class="my-container common-border-shadow offer">
