<script>
import Draggable from "vuedraggable";

import top from "@/assets/svg/top.svg";
import i from "@/assets/svg/i.svg";
import eye from "@/assets/svg/eye.svg";
import searchIcon from "@/assets/svg/search.svg";
import arrowDown from "@/assets/svg/arrow-down.svg";
import { fileUploaderOptionsThumbnail } from "@/assets/constants.js";

import Editable from "./components/Editable.vue";
import Toggle from "./components/Toggle.vue";
import Item from "./components/Item.vue";

export default {
  name: "App",
  components: { Toggle, Item, Editable, Draggable },
  data() {
    return {
      // *****
      // START TO SEND, THIS DATA BELOW NEEDS TO BE SENT TO THE API

      // quote settings
      internalQuoteName: "",
      internalQuoteId: "",
      language: { label: "Slovenina", value: "sk", flag: "sk.svg" },
      currency: "EUR",
      nameOfClient: "",
      color: "#F3385A",
      deliveryDateTitle: "",
      deliveryDateText: "",
      validityDate: null,
      hideDofferoLogo: false,

      //template select
      templateSelect: "Vianon ponuka",
      // top inputs
      title: "",
      welcomeText: "",
      companyName: "",
      companyStreet: "",
      companyTownZipcode: "",
      ico: "",
      dic: "",
      icDph: "",
      approveDeclinePriceQuote: false,
      addDeliveryDate: false,
      showReviews: false,
      logo: null,
      // summaries
      summaries: [],
      newSummaryTitle: "",
      newSummaryValue: "",
      //bottom
      leftTextArea: "",
      rightTextArea: "",
      signature: null,
      // items
      items: [],
      // global discounts and vat
      totalDiscountValue: 15,
      vatValue: 21,

      // END TO SEND
      // ********

      // options for selects
      defaultColors: [
        "#F3385A",
        "#FF551F",
        "#FFE299",
        "#6CC65E",
        "#A4E6F4",
        "#3960C6",
        "#BD00FF",
        "#A40000",
        "#B3B3B3",
        "#000000",
      ],
      selectOptions: ["Vianon ponuka", "awef", "awef"],
      languageOptions: [
        { label: "Slovenina", value: "sk", flag: "sk.svg" },
        { label: "English", value: "en", flag: "en_GB.svg" },
        { label: "Greek", value: "el", flag: "el.svg" },
      ],
      currencyOptions: ["USD", "GBP", "EUR", "CZK"],
      clients: ["client 1", "client 2", "good client"],

      // toggles
      showFileUpload: false,
      showTemplateSelect: false,
      drag: false,
      showSummaryNumbers: false,
      addTotalEnd: false,
      totalDiscount: false,
      vatEnable: false,

      // icons
      top,
      i,
      eye,
      searchIcon,
      arrowDown,

      // init item data
      initItem: {
        title: "",
        type: {
          value: 1,
          label: "Cena za hodinov sadzbu",
        },
        images: null,
        types: [
          {
            value: 1,
            label: "Cena za hodinov sadzbu",
          },
          {
            value: 2,
            label: "Cenov rozsah od - do",
          },
          {
            value: 3,
            label: "Hodinov rozsah od -do",
          },
          {
            value: 4,
            label: "Vlastn cena",
          },
          {
            value: 5,
            label: "Celkov cena z podpoloiek",
          },
        ],
        richText: "",
        hoursFrom: 0,
        imagesUploadedLength: 0,
        hoursTo: 0,
        priceFrom: 0,
        priceTo: 0,
        rate: 0,
        hours: 0,
        count: 0,
        value: 0,
        unit: null,
        subItems: [],
        total: 0,
        totalWithSubitems: 0,
        totalWithSubitemsWithDiscount: 0,
        timestamp: null,
        note: null,
        hasDiscount: false,
        discount: 0, // percent
        doNotCountInTotal: false,
      },
    };
  },
  computed: {
    currencySymbol() {
      let currency;
      switch (this.currency) {
        case "GBP":
          currency = "";
          break;
        case "USD":
          currency = "$";
          break;
        case "CZK":
          currency = "K";
          break;
        case "EUR":
        default:
          currency = "";
      }

      return currency;
    },
    summariesTotal() {
      let total = 0;
      this.summaries.forEach(({ value }) => {
        const valueNumber = parseFloat(value);

        if (!isNaN(valueNumber)) total += valueNumber;
      });

      if (this.newSummaryValue) {
        const newSummaryValueNumber = parseFloat(this.newSummaryValue);

        if (!isNaN(newSummaryValueNumber)) total += newSummaryValueNumber;
      }

      return isNaN(total) ? 0 : total;
    },
    total() {
      let finalTotal = { from: 0, to: 0 };
      this.items.forEach((item) => {
        if (!item.doNotCountInTotal) {
          if (
            item.totalWithSubitemsWithDiscount.from ||
            item.totalWithSubitemsWithDiscount.from === 0
          )
            finalTotal.from += parseFloat(
              item.totalWithSubitemsWithDiscount.from
            );
          if (
            item.totalWithSubitemsWithDiscount.to ||
            item.totalWithSubitemsWithDiscount.to === 0
          )
            finalTotal.to += parseFloat(item.totalWithSubitemsWithDiscount.to);
          else if (typeof item.totalWithSubitemsWithDiscount === "number") {
            finalTotal.from += parseFloat(item.totalWithSubitemsWithDiscount);
            finalTotal.to += parseFloat(item.totalWithSubitemsWithDiscount);
          }
        }
      });
      if (finalTotal.from === finalTotal.to) finalTotal.sum = finalTotal.from;
      return finalTotal;
    },
    totalWithDiscount() {
      const percentage = (100 - parseFloat(this.totalDiscountValue)) / 100;

      return {
        from: this.totalDiscount
          ? (this.total.from * percentage)?.toFixed(2)
          : this.total.from?.toFixed(2),
        to: this.totalDiscount
          ? (this.total.to * percentage)?.toFixed(2)
          : this.total.to?.toFixed(2),
        sum: this.totalDiscount
          ? (this.total.sum * percentage)?.toFixed(2)
          : this.total.sum?.toFixed(2),
      };
    },
    totalWith21VAT() {
      const total = this.totalWithDiscount;

      const percentage = (100 + parseFloat(this.vatValue)) / 100;

      return {
        from: parseFloat((total.from * percentage).toFixed(2)),
        to: parseFloat((total.to * percentage).toFixed(2)),
      };
    },
    filteredClients() {
      const nameOfClient = this.nameOfClient?.toLowerCase();

      if (this.clients.includes(nameOfClient)) return [];

      if (nameOfClient.length <= 3) return [];
      else
        return this.clients.filter((client) =>
          client?.includes(nameOfClient?.toLowerCase())
        );
    },
  },
  methods: {
    enableFileUpload() {
      window.$(`.file-upload input.files`).fileuploader({
        theme: "default",
        limit: 10,
        thumbnails: fileUploaderOptionsThumbnail,
        extensions: ["jpg", "jpeg", "png", "bmp", "gif", "svg"],
        // Callback fired on selecting and processing a file
        onSelect: () => {
          const input = document.querySelector(`.file-upload input.files`);

          this.images = input.files;
          console.log(this.images);
          this.imagesUploadedLength = input?.files?.length;
        },
        onRemove: () => {
          this.imagesUploadedLength -= 1;
        },
      });

      this.removeFileUploaderText();
      setTimeout(() => {
        this.removeFileUploaderText();
      }, 300);
    },
    removeFileUploaderText() {
      // remove text
      const fileUploader = document.querySelector(`.file-upload .fileuploader`);
      if (fileUploader) {
        // console.log(fileUploader.nextSibling);
        fileUploader?.nextSibling?.remove();
      }
    },
    triggerFileUpload() {
      const input = document.querySelector(
        `.file-upload .file-outer .fileuploader-input-caption`
      );
      // console.log(input);
      input?.click();
    },
    addClient(name) {
      this.clients.push(name);
    },
    selectNameClient(client) {
      this.nameOfClient = client;
    },
    finish() {
      console.log(this);
    },
    clickCustomColor() {
      console.log(
        "length",
        this.defaultColors.filter((col) => col === this.color).length
      );

      if (this.defaultColors.filter((col) => col === this.color).length > 0) {
        this.color = "#eeeeee";
        setTimeout(() => document.querySelector(".color-input")?.click(), 300);
      }
    },
    deleteItem(index, itemsContainer = this.items) {
      itemsContainer.splice(index, 1);
    },
    logoInput(e) {
      const input = e.target;

      const file = input?.files?.[0];
      if (file) this.logo = URL.createObjectURL(file);
    },
    signatureInput(e) {
      const input = e.target;

      const file = input?.files?.[0];
      if (file) this.signature = URL.createObjectURL(file);
    },
    clickSignature() {
      const signatureInput = document.querySelector(".signature-input");
      console.log;
      if (signatureInput) signatureInput.click();
    },
    addNewSummary() {
      if (this.summaries.length >= 2) return;

      this.summaries.push({
        title: this.newSummaryTitle,
        value: this.newSummaryValue,
      });

      this.clearSummaryInputs();
    },
    deleteSummary(index) {
      console.log("deletesummary: ", index);
      this.summaries.splice(index, 1);
    },
    clearSummaryInputs() {
      this.newSummaryTitle = "";
      this.newSummaryValue = "";
    },
    addNewItem() {
      this.items.push(
        JSON.parse(JSON.stringify({ ...this.initItem, timestamp: Date.now() }))
      );
    },
    moveItem(from, to, itemsContainer) {
      // remove `from` item and store it
      var f = itemsContainer.splice(from, 1)[0];
      // insert stored item into position `to`
      itemsContainer.splice(to, 0, f);
    },
    moveUp(index, itemsContainer = this.items) {
      this.moveItem(index, index - 1, itemsContainer);
    },
    moveDown(index, itemsContainer = this.items) {
      this.moveItem(index, index + 1, itemsContainer);
    },
    addSubItem(index) {
      this.items[index].subItems.push({
        ...this.initItem,
        subItems: null,
        timestamp: Date.now(),
      });
    },
    enableTooltips() {
      var tooltipTriggerList = [].slice.call(
        document.querySelectorAll('[data-bs-toggle="tooltip"]')
      );
      tooltipTriggerList.map(function (tooltipTriggerEl) {
        return new window.bootstrap.Tooltip(tooltipTriggerEl);
      });
    },
  },
  mounted() {
    this.addNewItem();

    this.enableTooltips();
    this.enableFileUpload();
  },
};
</script>

