  .info {
    font-family: "Mont";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 182.5%;
    color: $black;
    text-align: left;
  }

  .my-container {
    &.offer {
      margin-top: 30px;
    }
  }

  .offer-footer {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    textarea:focus {
      box-shadow: 0px 2px 2px rgb(0 0 0 / 9%) !important;
      border-color: #90c1ea !important;
      outline: none;
    }

    .box {
      padding: 10px;
      min-width: 200px;

      .signature-input {
        display: none;
      }

      &.stacked {
        margin-bottom: 10px;
        + .stacked {
          margin-top: 10px;
        }
      }
    }

    .left {
    }

    .middle {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      .middle-text {
        font-family: "Mont";
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 182.5%;
        color: $black;
      }
    }

    .right {
    }
