  .i-icon {
    color: #9598a7;
    width: 18px;
    height: 18px;
    cursor: pointer;

    &.no-circle {
      border: none;
    }
  }

  .do-not-count-in-total-toggle {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: $black;
    justify-content: center;
    display: flex;
    position: relative;
    align-items: center;

    &.root-toggle-count-total {
      width: 100%;
      display: flex;
      justify-content: flex-end;
    }

    .i-icon {
      margin-left: 10px;
    }
  }

  .form-floating {
    font-family: "Mont";
    font-style: normal;
    font-weight: 700;
    font-size: 10px;
    line-height: 182.5%;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: #a6a8b3;
    flex-grow: 1;

    label {
      text-transform: uppercase;
    }
  }

  .items-container {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
  }

  input {
    outline: none;
    margin-top: 0;
  }

  .flex-container {
    display: flex;
    align-items: center;
  }

  .accordion-header {
    &.my-blue {
      color: white;
    }
  }

  .accordion-body {
    .my-container {
      padding: 0;
      gap: 0;
      border-radius: 0;
    }
  }

  .accordion-item {
    &.my-blue {
      .accordion-button {
        background: $black;
        outline: none;
        box-shadow: none;
        color: white;
        font-family: "Mont";
        font-style: normal;
        font-weight: 800;
        font-size: 20px;
        line-height: 182.5%;

        svg {
          margin-left: 10px;
          margin-right: 10px;
        }

        &:after {
          transform: none;
          background-image: url('data:image/svg+xml,<svg width="8" height="7" viewBox="0 0 12 7" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M10.3333 1L5.66659 5.66667L0.999919 1" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/></svg>');
        }

        &:not(.collapsed):after {
          background-image: url('data:image/svg+xml,<svg width="12" height="7" viewBox="0 0 12 7" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M10.3334 6L5.66671 1.33333L1.00004 6" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/></svg>');
        }
      }
    }
  }

  .blue-container {
    padding: 20px 40px;
    background: $black;
    border-radius: 4px;

    text-align: left;
    color: white;

    .price {
      white-space: nowrap;
    }

    .blue-container-title {
      font-family: "Mont";
      font-style: normal;
      font-weight: 800;
      font-size: 20px;
      line-height: 182.5%;
      margin-left: 10px;
    }

    &.totals {
      max-width: 580px;
      float: right;

      .total-without-vat,
      .total-with-vat {
        display: flex;
        justify-content: space-between;

        font-family: "Mont";
        font-style: normal;
        font-weight: 800;
        line-height: 182.5%;
        color: white;
        margin: 10px 0;
      }

      .total-without-vat {
        font-size: 14px;

        .price {
          font-size: 24px;
        }
      }

      .top-price {
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid white;
        font-size: 18px;

        del {
          position: relative;
          font-family: "Mont";
          font-style: normal;
          font-weight: 800;
          font-size: 18px;
          line-height: 182.5%;
        }

        .red-badge {
          background: #db355f;
          padding: 2px 5px;
          position: absolute;
          top: 5px;
          bottom: 0;
          left: -80px;
          font-family: "Mont";
          font-style: normal;
          font-weight: 800;
          font-size: 12px;
          line-height: 18px;
          height: 20px;
          border-radius: 2px;
        }
      }

      .total-with-vat {
        font-size: 18px;

        .price {
          font-size: 34px;
        }
      }
    }

    .totals-toggles {
      display: flex;
      text-align: left;
      margin-bottom: 10px;

      .toggle {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;

        &:first-of-type {
          .switch {
            margin-left: 0;
          }
        }
      }
    }
  }

  .common-border-shadow {
    background: #ffffff;
    border: 1px solid #dadada;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
  }

  .no-top-border-radius {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }

  .no-bottom-border-radius {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  .my-container {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 32px 40px;
    gap: 16px;
  }

  .file-upload {
    width: 100%;

    * {
      text-transform: none !important;
    }
  }

  .arrow-down-icon {
    padding: 20px;
    cursor: pointer;
    transition: all 0.2s;

    &.reversed {
      transform: rotate(180deg);
    }
  }

  .upload-files-cloud {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 15px;
    background: #ffffff;
    border: 1px dashed #e1e2e3;
    border-radius: 10px;
    cursor: pointer;
    user-select: none;

    span.text {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;

      color: #c2c3c9;
    }
  }

  .template-use {
    margin: 20px 0;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }

  .template-use-select {
    width: 100%;
    margin-top: 10px;
    max-width: none;
    min-width: 0;
  }

  .green-button,
  .simple-button,
  .gray-button {
    user-select: none;
  }

  .green-button,
  .simple-button {
    border: 1px solid $green;
    border-radius: 4px;
    padding: 20px 32px;
    cursor: pointer;
    color: $green;
    text-align: center;
    font-weight: 600;
  }

  .simple-button {
    color: $black;
    border: 1px solid #dde3e8;
  }

  .gray-button {
    background: #f7f8f8;
    border-radius: 4px;
    padding: 20px 32px;
    font-family: "Quicksand";
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    text-align: center;
    color: $black;
    cursor: pointer;

    svg {
      margin-right: 10px;
    }

    .i-icon {
      margin-left: 10px;
    }
  }

  .green-button {
    border: 1px solid $green;
    color: $green;

    &.filled {
      background: $green;
      color: white;
    }

    + .green-button {
      margin-left: 20px;
    }
  }

  .template-use-title {
    font-family: "Mont";
    font-style: normal;
    font-weight: 800;
    font-size: 20px;
    line-height: 182.5%;
    color: $black;
  }

  .template-use-desc {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: $black;
  }

  .big-input-title {
    font-size: 36px;
    font-style: normal;
    font-weight: 800;
    font-size: 36px;
    line-height: 77px;
    width: 100%;
    color: $black;
    padding: 10px 30px;
  }

  .input-welcome-text {
    color: $black;
    width: 100%;
    padding: 24px 30px;
  }

  .top-inputs {
    display: flex;
    width: 100%;
  }

  .logo-upload-text {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-align: right;
    text-decoration-line: underline;
    cursor: pointer;
    flex-shrink: 1;
    padding-right: 15px;

    color: #2ed8b6;

    input {
      display: none;
    }
  }

  .logo-input-container {
    flex: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;

    img {
      max-width: 200px;
      max-height: 100px;
    }

    .logo-input {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 700;
      font-size: 14px;
      line-height: 16px;
      text-align: right;
      text-decoration-line: underline;

      color: $green;
    }
  }

  .small-inputs-container {
    flex: 1;
    margin-left: 10px;
    padding: 15px;

    input {
      font-family: "Mont";
      font-style: normal;
      font-weight: 600;
      font-size: 14px;
      line-height: 18px;
      /* identical to box height */
      outline: none;

      display: flex;
      align-items: center;

      color: rgba(33, 37, 65, 0.5);

      width: 100%;
      padding-bottom: 5px;
      margin: 10px 0;
      border-bottom: 1px solid #dde3e8;

      &:focus {
        border-bottom-color: #9fa4a7;
      }
    }
  }

  .toggles {
    display: flex;
    justify-content: center;
    align-items: center;

    .toggle-with-tooltip {
      display: flex;
      align-items: center;

      .i-icon {
        margin-left: 10px;
        width: 15px;
        height: 15px;
        margin-bottom: 5px;
      }
    }
  }

  .toggle {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .input-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px 16px;
    justify-content: space-between;

    background: #ffffff;
    border: 1px solid #dde3e8;
    border-radius: 6px;
    margin-bottom: 10px;

    &:focus-within {
      box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.09) !important;
      border-color: #90c1ea !important;
    }

    label {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 700;
      font-size: 11px;
      line-height: 13px;
      text-align: left;
      /* identical to box height */

      letter-spacing: 0.1em;
      text-transform: uppercase;
      display: flex;
      flex-direction: column;
      width: 100%;

      color: #9598a7;

      input {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;

        color: $black;
        padding-top: 10px;
      }
    }
  }

  .quote-settings {
    width: 100%;
    display: flex;
    flex-direction: row;

    .quote-side {
      flex-grow: 1;
      width: 50%;
      padding: 0 15px;

      .client-boxes {
        display: flex;
        margin: 10px 0;
        flex-wrap: wrap;

        .client-box {
          margin: 5px;
          background: #f2f7fb;
          border-radius: 2px;
          font-family: "Mont";
          font-style: normal;
          font-weight: 600;
          font-size: 12px;
          line-height: 15px;
          padding: 3px 10px;
          cursor: pointer;

          color: $black;
        }
      }

      .quote-selects-flex {
        display: flex;
        margin-left: -5px;
        margin-right: -5px;

        > * {
          width: 50%;
          margin: 0 5px;
        }

        .multiselect {
          height: 60px;
        }

        .multiselect__select {
          height: 58px;
        }
      }

      .hide-logo-toggle-container {
        display: flex;
        justify-content: flex-start;
        align-items: center;
      }

      .toggles {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        .toggle {
          margin-bottom: 8px;
        }
      }

      &.left {
        border-right: 2px solid #dde3e8;
      }

      &.right {
        .quote-title {
          font-family: "Roboto";
          font-style: normal;
          font-weight: 700;
          font-size: 16px;
          line-height: 19px;
          color: $black;
          text-align: left;
        }

        .quote-subtitle {
          font-family: "Roboto";
          font-style: normal;
          font-weight: 400;
          font-size: 14px;
          line-height: 16px;
          text-align: left;
          margin-top: 5px;

          color: #9598a7;
        }

        .colors {
          display: flex;
          margin-top: 20px;
        }

        .cpicker {
          min-width: 200px;
          cursor: pointer;
          border: 1px solid #dde3e8;
          margin: 8px 0;
        }

        .custom-color-button {
          background: #ffffff;
          border: 1px solid #dde3e8;
          box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
          border-radius: 3px;
          padding: 10px;
          margin: 10px 0;
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;

          &.selected {
            border-color: #555;
          }
        }

        .color-input {
          height: 25px;
          width: 25px;
          margin-left: 10px;
          border: 1px solid #dde3e8;
          box-shadow: 0px 4px 10px rgb(0 0 0 / 10%);
          border-radius: 3px;
          width: 28px;
          height: 28px;
          margin: 0 5px;
          padding: 5px;
          background: white;
          cursor: pointer;
        }

        .color-box {
          border: 1px solid #dde3e8;
          box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
          border-radius: 3px;
          width: 28px;
          height: 28px;
          margin: 0 5px;
          padding: 5px;
          cursor: pointer;

          &.selected {
            border: 1px solid #555;
          }

          .inner {
            height: 100%;
            width: 100%;
          }
        }
      }
    }
  }

  .summaries-container {
    width: 100%;
  }

  .summaries {
    display: flex;
    width: 100%;
    margin-top: 10px;

    .summary {
      margin: 0 10px;
      min-width: 150px;
      position: relative;
      max-width: 200px;

      &:first-of-type {
        margin-left: 0;
      }

      &.new {
        max-width: 250px;

        input {
          max-width: 130px;
          padding-top: 2px;
          border-bottom: 1px solid #dde3e8;
          outline: none;

          &.input-new-summary-title {
            font-size: 12px;
            height: 30px;
            width: 100%;
            font-weight: 600;
          }

          &.input-new-summary-value {
            width: 100%;
            font-weight: 600;
            font-family: "Mont";
          }
        }
      }

      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: 24px 28px;
      padding-top: 15px;
      border-radius: 8px;

      font-family: "Mont";
      font-style: normal;
      font-weight: 800;
      font-size: 24px;
      line-height: 31px;
      flex-grow: 1;
      color: $black;
      border: 1px solid $black;

      .delete-btn {
        color: #9598a7;
        font-size: 12px;
        position: absolute;
        right: 10px;
        top: 10px;
        cursor: pointer;
      }

      .summary-title {
        font-family: "Mont";
        font-style: normal;
        font-weight: 800;
        font-size: 14px;
        line-height: 182.5%;
        width: 100%;
        text-align: left;
        outline: none;

        &.editable {
          width: auto;
          min-width: 20px;
          padding-top: 7px;
        }
      }

      .summary-amount {
        font-family: "Mont";
        font-style: normal;
        font-weight: 800;
        font-size: 24px;
        line-height: 31px;
        width: 100%;
        text-align: left;
        min-width: 50px;
        outline: none;
      }

      &.total {
        background: $black;
        color: white;
        border: none;
        max-width: 300px;
        margin-right: 0;
        padding-top: 22px;
      }
    }
  }

  .add-new-summary-btn {
    padding: 13px 30px;
    cursor: pointer;
    width: 250px;
    margin-top: 20px;
    margin-bottom: 10px;
    user-select: none;

    &.disabled {
      opacity: 0.7;
      cursor: not-allowed;
    }
  }

  .switch {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 19px;
    margin-right: 5px;
    margin-left: 10px;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 15px;
    width: 15px;
    left: 3px;
    bottom: 2px;
    background-color: white;
    transition: 0.4s;
  }

  input:checked + .slider {
    background-color: $green;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px $green;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(16px);
    -ms-transform: translateX(16px);
    transform: translateX(16px);
  }

  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }

  .blue-button {
    &.large {
      background: $black;
      border-radius: 4px;
      font-family: "Roboto";
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      text-align: center;
      color: #ffffff;
      width: 100%;
      padding: 22px 40px;
      margin: 5px 0;
      cursor: pointer;
    }
  }

  .simple-bottom-border-input {
    border: none;
    outline: none;
    box-shadow: none;
    padding: 0 2px;
    border-bottom: 1px solid #c7d4eb;
    background: transparent;
    color: white;
    height: 25px;
    width: 25px;
  }

  .total-toggles-container {
    display: flex;
    align-items: center;

    .form-check {
      flex-grow: 1;
      margin-right: 7px;
    }

    + .total-toggles-container {
      margin-left: 20px;
    }
  }
}

@media screen and (max-width: 848px) {
  #quote-creation {
    padding: 15px;

    .offer-footer,
    .footer-buttons,
    .final-buttons {
      flex-direction: column;
    }

    .footer-buttons {
      .left,
      .right {
        flex-direction: column;

        .green-button {
          margin: 10px 0;
        }
      }

      .left {
        .simple-button {
          + .simple-text {
            margin-left: 0;
            margin-top: 10px;
          }
        }
      }
    }

    .final-buttons {
      .gray-button {
        margin: 10px 0;
        + .gray-button {
          margin-left: 0;
        }
      }
    }

    .accordion-body {
      padding: 10px;
    }

    .toggles {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;

      .form-check.form-switch {
        margin-left: 0;
      }
    }

    .top-inputs {
      flex-direction: column;

      .small-inputs-container {
        margin: 10px 0;
      }
    }

    .colors {
      flex-wrap: wrap;

      .color-box {
        margin: 5px !important;
      }
    }

    .form-floating {
      &.title {
        max-width: none;
        min-width: 0;
        width: auto;
      }

      &.unit-select {
        margin-bottom: 20px;
        width: 100%;
        min-width: 0;
        max-width: 100%;

        .multiselect,
        .multiselect__tags {
          width: 100%;
          min-width: 0;
          max-width: 100%;
        }
